<template>
    <nacl-page-container no-header>
        <!-- <nacl-header>意见反馈</nacl-header> -->
        <nacl-page-content>
            <div class="content">
                <div class="text-area">
                    <textarea
                        v-model="text"
                        placeholder="请输入您的宝贵意见和问题…"
                        rows="10"
                    />
                </div>
                <div class="btn-wrap">
                    <nacl-button type="primary" radius @click="submit">提交反馈</nacl-button>
                </div>
            </div>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import { NaclButton } from 'nacl-ui'
export default {
    name: 'feedback',
    components: { NaclButton },
    data () {
        return {
            text: ''
        }
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        submit () {
            alert('我们已经收到您的意见以及反馈,感谢以往您对我们的支持,谢谢!')
            // this.$router.history.go(-1)
            this.toGetUser();
            window.webkit.messageHandlers.goBack.postMessage('');
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
    .content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #fff;
        margin-top: 20px;
        padding: 10px 20px;
    }
    .text-area {
        margin-top: 32px;
        padding: 20px;
        background-color: #ffffff;
        border-radius: 15px;
        border: 2px solid #e3e3e3;
        box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.1);
        textarea {
            width: 100%;
        }
    }
    .btn-wrap {
        padding: 0 30px;
        margin-top: 60px;
    }
</style>
